* {
    box-sizing: border-box;
}

body {
    margin: 0;
    padding: 0;
    background-color: white;
    font-family: 'Press Start 2P', cursive;
}

#board {
  width: 644px;
  height: 644px;
  margin: 1em auto;
  border: 2px solid black;
}
#board  > div {
  // border: 1px solid black;
  float: left;
  width: 64px;
  height: 64px;
}

.man {
  background-image: url('../images/man.png');
  background-repeat: no-repeat;
  background-size: 100;
  background-position: center;
}
.man_walking {
  background-image: url('../images/man_walking.png');
  background-repeat: no-repeat;
  background-size: 100;
  background-position: center;
}
.man_standing {
  background-image: url('../images/man_standing.png');
  background-repeat: no-repeat;
  background-size: 100;
  background-position: center;
}
.man_sitting {
  background-image: url('../images/man_sitting.png');
  background-repeat: no-repeat;
  background-size: 100;
  background-position: center;
}
.house {
  background-image: url('../images/house.png');
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}
.tree {
  background-image: url('../images/tree.png');
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}
.zombie {
  background-image: url('../images/zombie.png');
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}

section#over {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: 1;
  padding: 10px;
  background-color: rgba(255, 0, 0, 0.75);
}

section#over pre {
  display: inline-block;
  width: 100%;
  text-align: center;
  font-size: 10px;
  font-weight: bolder;
  color: brown;
}

.invisible {
  display: none;
}
